'use client';

import { useSearchParams } from 'next/navigation';

function formatMatchingAnalysis(analysis) {
  return {
    'Match Score': `${(analysis.matchValue * 100).toFixed(2)}%`,
    Gender: analysis.gender || 'Unknown',
    'Body Shape': analysis.bodyShape || 'Unknown',
    'Skin Tone': analysis.skinTone || 'Unknown',
    'Clothing Style': analysis.clothingStyle || 'Unknown',
    'Color Match': analysis.colorMatch || 'Unknown',
    'Fit Assessment': analysis.fitAssessment || 'Unknown',
    'Overall Impression': analysis.overallImpression || 'Unknown',
  };
}

export default function MatchingResultPage() {
  const searchParams = useSearchParams();
  const matchingAnalysisParam = searchParams.get('matchingAnalysis');
  const matchingAnalysis = matchingAnalysisParam ? JSON.parse(decodeURIComponent(matchingAnalysisParam)) : null;

  if (!matchingAnalysis) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-[#0f0c29] via-[#302b63] to-[#24243e] text-white flex items-center justify-center">
        <p className="text-xl">No analysis data provided.</p>
      </div>
    );
  }

  const analysisLines = formatMatchingAnalysis(matchingAnalysis);
  const matchValue = analysisLines['Match Score'];

  return (
    <div className="min-h-screen bg-gradient-to-b from-[#0f0c29] via-[#302b63] to-[#24243e] text-white flex items-center justify-center px-6">
      <div className="w-full max-w-3xl rounded-2xl p-8 bg-[#1a1a2f] shadow-2xl backdrop-blur-md bg-opacity-80">
        <h1 className="text-4xl font-bold text-center mb-10 text-white">Matching Analysis Results</h1>

        <div className="flex justify-center mb-10">
          <div className="w-40 h-40 rounded-full bg-gradient-to-r from-purple-600 to-indigo-500 flex items-center justify-center text-white text-2xl font-bold shadow-xl">
            {matchValue}
          </div>
        </div>

        <ul className="space-y-4">
          {Object.entries(analysisLines).map(([key, value]) => {
            if (key === 'Match Score') return null;
            return (
              <li key={key} className="bg-[#24243e] p-4 rounded-xl shadow-md border border-gray-700">
                <span className="font-semibold text-purple-300">{key}:</span>{' '}
                <span className="text-white">{value}</span>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}